.uberbox-lightbox-wrapper {
	position: absolute;
	z-index: 100002;
	left: 0px;
	right: 0px;
	bottom: 0px;
	top: 0px;
	.uberbox-lightbox-content {
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0; } }
.uberbox-next, .uberbox-prev {
	position: fixed;
	color: white;
	font-size: 54px;
	cursor: pointer;
	z-index: 5;
	@include hover-active;
	background: transparent;
	user-select: none;
	&.uberbox-disabled {
		opacity: 0.15;
		cursor: default;
		&:hover {
			opacity: 0.15; } }
	&:active, &:focus {
		outline: none;
		background: transparent; }
	i {
		position: absolute;
		display: block; } }

.uberbox-vertical {
	.uberbox-next, .uberbox-prev {
		left: 50%; }
	.uberbox-next, .uberbox-prev {
		left: 20%;
		right: 20%;
		height: 90px; }
	.uberbox-prev {
		top: 0;
		i {
			@include uberbox-icon;
			top: 9px;
			margin-left: -26px;
			left: 50%;
			border-radus: 0 0 4px 4px;
			&:before {
				content: '\e800';
				transform: rotate(-90deg);
				transform-origin: center; } } }
	.uberbox-next {
		bottom: 0;
		i {
			@include uberbox-icon;
			bottom: 9px;
			left: 50%;
			margin-left: -26px;
			border-radius: 4px 4px 0 0;
			&:before {
				content: '\e800';
				transform: rotate(90deg);
				transform-origin: center; } } }

	&.uberbox-has-carousel {
		.uberbox-next, .uberbox-prev {
			margin-left: -180px; } } }
.uberbox-horizontal {
	.uberbox-next, .uberbox-prev {
		top: 45px;
		bottom: 0px;
		width: 60px;
		i {
			text-align: center;
			display: inline-block;
			width: 100%;
			@include uberbox-icon;
			margin: -38px 0 0 0;
			padding: 15px 0;
			top: 50%;
			&:before {
				margin: 0; } } }
	&.uberbox-has-carousel {
		.uberbox-next, .uberbox-prev {
			bottom: 130px;
			i {
				margin-top: -28px; } } }
	.uberbox-prev {
		left: 0;
		width: 54px;
		i {
			border-radius: 0 4px 4px 0;
			&:before {
				content: '\e800';
				transform: rotate(180deg);
				transform-origin: 50% 50%; } } }
	.uberbox-next {
		right: 0;
		i {
			border-radius: 4px 0 0 4px;
			&:before {
				content: '\e800';
				transform-origin: 50% 50%; } } } }
@media screen and (orientation: portrait) {
	.uberbox {
		.uberbox-next, .uberbox-prev {
			font-size: 20px;
			height: 42px;
			width: 30px;
			bottom: auto;
			line-height: 42px;
			i {
				margin: none;
				position: static;
				padding: 0;
				display: inline; } }
		.uberbox-prev {
			left: 12px;
			top: 3px; }
		.uberbox-next {
			left: 36px;
			top: 3px; } } }
.uberbox-lightbox-item {
	position: fixed;
	z-index: 1;
	left: 6px;
	top: 42px;
	right: 6px;
	bottom: 20px;
	transition: opacity 0.3s;
	opacity: 0;
	&.uberbox-enable-transition {
		transition: all 0.3s;
		opacity: 1; }
	&.uberbox-current {
		z-index: 2; }
	&.uberblox-loaded {
		opacity: 1; }

	@include respond-to(handhelds) {
		left: 6px;
		top: 45px;
		right: 6px;
		bottom: 6px; } }

.uberbox-vertical {
	.uberbox-lightbox-item {
		@include respond-to(wide-screens) {
			left: 30px;
			right: 30px;
			top: 90px;
			bottom: 90px; } }
	&.uberbox-has-carousel .uberbox-lightbox-item {
		right: 30px;
		@include respond-to(wide-screens) {
			right: 180px; } } }

&.uberbox-horizontal {
	.uberbox-lightbox-item {
		@include respond-to(wide-screens) {
			left: 0;
			right: 0;
			bottom: 45px;
			top: 45px; } }
	&.uberbox-has-carousel {
		@include respond-to(wide-screens) {
			.uberbox-lightbox-item {
				bottom: 130px; } } } }
.uberbox-lightbox-item {
	&.uberbox-no-description {
		.uberbox-lightbox-item-content-wrapper {
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			transform: none; }
		.uberbox-item-object {
			position: absolute;
			bottom: 0;
			top: 0;
			left: 0;
			right: 0; }
		.uberbox-iframe-content {
			width: 100%;
			height: 100%;
			iframe {
				width: 100%;
				height: 100%; } }
		.uberbox-image-content {
			width: 100%;
			height: 100%;
			img {
				position: absolute;
				max-width: 100%;
				max-height: 100%;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%); } } }


	&.uberbox-description-mini {
		.uberbox-item-object {
			bottom: 50px;
			top: 0;
			left: 0;
			right: 0;
			> div {
				max-width: 100%;
				max-height: 100%; } }
		.uberbox-lightbox-item-content-wrapper {
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			transform: none; }
		.uberbox-item-description {
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translate(-50%, 0);
			padding: 10px 0 0 0;
			box-sizing: content-box;
			line-height: 18px;
			margin: 0 auto;
			color: white;
			font-family: myriad-pro, 'Myriad Pro', 'Helvetica Neue', Helvetica, Arial, sans-serif;
			line-height: 20px;
			height: 40px;
			overflow-y: hidden;
			font-smoothing: auto;
			text-rendering: optimizeLegibility;
			font-weight: 200;
			font-size: 11px;
			color: #bbb;
			text-align: center;
			h2 {
				font-size: 13px;
				font-weight: normal;
				line-height: 20px;
				font-family: myriad-pro, 'Myriad Pro', 'Helvetica Neue', Helvetica, Arial, sans-serif;
				margin: 0;
				padding: 0;
				color: #ccc; } }
		.uberbox-iframe-content {
			width: 100%;
			height: 100%;
			iframe {
				width: 100%;
				height: 100%; } } }
	&.uberbox-description-mini, &.uberbox-no-description {
		&.uberbox-natural-fit {
			.uberbox-item-object {
				position: absolute;
				left: 60px;
				right: 60px;
				bottom: 50px;
				top: 0; }
			.uberbox-image-content {
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				img {
					display: block;
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					max-height: 100%;
					max-width: 100%; } } }
		&.uberbox-fit-width {
			.uberbox-image-content {
				position: absolute;
				top: 50%;
				transform: translate(0, -50%);
				img {
					width: 100%;
					margin: 0 auto; } } }
		&.uberbox-fit-height {
			.uberbox-image-content {
				position: absolute;
				left: 0;
				right: 0;
				top: 0;
				bottom: 0;
				img {
					height: 100%;
					display: block;
					margin: 0 auto; } } }
		@include respond-to(handhelds) {
			top: 0;
			.uberbox-item-object {
				position: absolute;
				left: 0;
				right: 0;
				top: 42px;
				bottom: 50px; }
			.uberbox-item-description {
				width: 90%; } }
		@include respond-to(handhelds-landscape) {
			.uberbox-item-object {
				bottom: 30px;
				top: 20px; }
			.uberbox-item-description {
				line-height: 12px;
				height: 26px;
				h2 {
					line-height: 12px; } } }

		@include respond-to(wide-screens) {
			.uberbox-item-object {
				position: absolute;
				left: 60px;
				right: 60px;
				bottom: 60px;
				top: 0; }
			.uberbox-item-description {
				width: 400px; } } }
	&.uberbox-no-description {
		.uberbox-item-object {
			bottom: 0; } }
	&.uberbox-description-right, &.uberbox-description-bottom {
		font-weight: 200;
		color: #444;
		line-height: 20px;
		font-size: 13px;
		&:after {
			display: none;
			clear: both; }
		h2 {
			color: #000;
			font-weight: 200;
			font-size: 24px;
			padding-bottom: 12px; }
		h3 {
			color: #888;
			font-weight: 200;
			text-transform: uppercase;
			font-size: 13px; }
		p {
			margin-bottom: 18px; }
		@include respond-to(wide-screens) {
			font-size: 16px;
			line-height: 28px;
			h2 {
				font-size: 32px;
				line-height: 40px;
				padding-bottom: 8px; }
			h3 {
				font-size: 15px;
				line-height: 20px;
				margin-bottom: 12px; }
			p {
				margin-bottom: 24px; } } }
	&.uberbox-description-right {
		.uberbox-item-description {
			background: #fff;
			box-sizing: border-box;
			overflow: auto; }
		@include respond-to(wide-screens) {
			.uberbox-lightbox-item-content-wrapper {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				width: 80%;
				height: 100%;
				max-width: 1200px; }
			.uberbox-item-object {
					position: static;
					float: none;
					margin-right: 55%; }
			.uberbox-image-content {
				height: 100%;
				overflow: hidden;
				img {
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					max-width: 100%;
					max-height: 100%; } }
			&.uberbox-fit-oversized {
				.uberbox-image-content {
					position: absolute;
					top: 0;
					bottom: 0;
					left: 0;
					right: 0;
					width: auto;
					height: auto; } }
			&.uberbox-fit-width-oversized {
				.uberbox-image-content img {
					width: 100%;
					max-height: none; } }
			&.uberbox-fit-height-oversized {
				.uberbox-image-content img {
					height: 100%;
					max-width: none; } }
			&.uberbox-fit-height {
				.uberbox-image-content img {
					transform: none;
					left: auto;
					right: 0;
					top: 0;
					height: 100%; } }
			.uberbox-item-description {
				position: absolute;
				top: 0;
				right: 0;
				width: 55%;
				bottom: 0;
				background: #fff;
				box-sizing: border-box;
				overflow: auto;
				padding: 30px 36px 20px 42px; } }
		@media screen and (max-width: 1024px) {
			overflow: scroll;
			.uberbox-lightbox-item-content-wrapper {
				max-height: 100%; }
			.uberbox-image-content {
				max-height: 600px;
				overflow: hidden;
				img {
					width: 100%; } }
			.uberbox-item-description {
				padding: 24px; } }


		&.uberbox-skin-dark {
			.uberbox-lightbox-item-content-wrapper {
				top: 0;
				bottom: 0;
				left: 30px;
				right: 30px;
				transform: none;
				width: auto; }
			.uberbox-item-description {
				color: white;
				background: transparent;
				h1, h2, h3 {
					color: white; } }
			.uberbox-item-object {
				width: 50%;
				iframe {
					width: 100%; } }
			.uberbox-item-description {
				width: 50%;
				padding-top: 0; } } }

	&.uberbox-description-bottom {
		overflow: scroll;
		.uberbox-lightbox-item-content-wrapper {
			max-height: 100%; }
		.uberbox-item-description, .uberbox-item-object {
			width: 650px;
			margin: 0 auto;
			max-width: 100%; }
		@include respond-to(handhelds-portrait) {
			width: 100%;
			bottom: 0;
			.uberbox-item-description, .uberbox-item-object, .uberbox-lightbox-item-content-wrapper {
				width: 100%; } }
		.uberbox-item-object {
			overflow: hidden;
			margin: 0 auto;
			.uberbox-image-content {
				overflow: hidden;
				width: 100%;
				position: relative;
				height: 300px;
				img {
					display: block;
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					width: 100.1%; } }
			.uberbox-iframe-content {
				iframe {
					width: 100%;
					height: 365px; } } }
		&.uberbox-fit-height-oversized {
			.uberbox-image-content {
				img {
					height: 100.1%;
					width: auto; } } }
		&.uberbox-center-vertically .uberbox-lightbox-item-content-wrapper {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%); }
		.uberbox-item-description {
			box-sizing: border-box;
			background: white;
			padding: 24px 36px 20px 36px; } }
	&.uberbox-description-right, &.uberbox-description-bottom {
		@include respond-to(handhelds-portrait) {
			position: fixed;
			top: 45px;
			left: 6px;
			right: 6px;
			bottom: 6px;
			width: auto;
			overflow: scroll;
			.uberbox-item-object {
				.uberbox-image-content img {
					display: block;
					width: 100%; } }
			.uberbox-item-description {
				background: white;
				padding: 24px; } } } }
.uberbox-description-mini {
	.uberbox-bandcamp-content iframe {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%); } }
.uberbox-bandcamp-content iframe {
	max-width: 700px; }
.uberbox-ajax-content {
		&.uberbox-center-vertical {
			position: absolute;
			left: 0;
			right: 0;
			top: 50%;
			transform: translate(0, -50%); }
		&.uberbox-scroll {
			position: fixed;
			overflow: scroll;
			top: 0; } }
.uberbox-ajax-content.uberbox-scroll {
	bottom: 20px;
	left: 40px;
	right: 40px;
	top: 45px; }
.uberbox-horizontal.uberbox-has-carousel .uberbox-ajax-content.uberbox-scroll {
	bottom: 130px; }
.uberbox-vertical.uberbox-has-carousel .uberbox-ajax-content.uberbox-scroll {
	right: 180px; }
.uberbox-unknown-content {
	position: absolute;
	left: 0;
	top: 0;
	bottom: 0;
	right: 0;
	background: url(../img/no-photo.png) center center no-repeat; }

