.uberbox-carousel-wrapper {
	position: fixed;
	z-index: 100003;
	display: none;
	@include respond-to(wide-screens) {
		display: block; }

	.uberbox-carousel-content {
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		overflow: visible;
		transition: transform 0.3s; }
	.uberbox-carousel-item {
		opacity: 0;
		position: absolute;
		transition: opacity 0.7s;
		cursor: pointer;
		background: black;
		&.uberbox-loaded {
			opacity: 1;
			transition: opacity 0.3s;
			&.uberbox-enable-transition {
				transition: opacity 0.3s; } }
		&.uberbox-faded {
			opacity: 0; }
		.uberbox-loader {
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			background: red; }
		img {
			display: block;

			opacity: 0.5;
			transition: opacity 0.3s;
			border-radius: 2px; }
		&:hover img {
			opacity: 1; }
		&.uberbox-current {
			cursor: default;

			img {
				opacity: 1; } } } }

.uberbox-vertical .uberbox-carousel-wrapper {
	right: 15px;
	bottom: 0;
	top: 0px;
	width: 150px;
	.uberbox-carousel-item img {
		width: 100%; } }
.uberbox-horizontal .uberbox-carousel-wrapper {
	left: 0px;
	bottom: 0px;
	height: 130px;
	right: 0px;
	.uberbox-carousel-item img {
		height: 100%; } }

